
.Node{
    border-radius: 100%;
    padding: 20px 20px;
    border:1px solid black;
    background-color: white;
}

.main{
    width:100vw;
    height:95vh;
}


/* Navigation Bar */
.nav-bar{
    margin: 0;
    padding: 5px;
    display: inline-block;
    
}
.nav-button{
    margin-left: 5px;
    margin-right: 5px;
    border-radius: 2px;
    background-color: violet;
}

.nav-button:hover{
    background-color: rgb(190,5,255)
}

.canvasOptions{
    padding: 5px;
}
.canvas-option{
    border-radius: 40px;
    margin-left: 2px;
    margin-right: 2px;
}
.canvas-option:hover{
	background-color: #BFBFBF;
}
/* Canvas and its elements*/
#canvas-container{
    display: inline-block;
    width: 95%;
    height: 90%;
    border: white dotted ;
    overflow: scroll;
    
    
}
#myCanvas{
    width: 500vw;
    height: 500vh;
}
.node{
    fill:white;
    stroke: black;
}
.node:hover{
    fill:#BFBFBF;
}
.markedNode{
    fill:rgb(143, 227, 255);
    stroke: black;
}
.markedNode:hover{
    fill:rgb(83, 108, 116);
}
.edge{
    stroke:white;
    stroke-width: 3px;
}
.edge:hover{
    stroke:gray; 
}
.edgeTextConnector{
    stroke: rgb(255, 255, 255);
    stroke-dasharray: 5,5;
    stroke-opacity: 40%;
    stroke-width: 2px;
}
.text{
    font-size:1.1em;
    text-anchor: middle;
    dominant-baseline: middle;
    
}
.edgeText{
    font-size:1.1em;
    text-anchor: middle;
    filter: drop-shadow(2px 0 0 black) drop-shadow(-2px 0 0 black) drop-shadow(0 2px 0 black) drop-shadow(0 -2px 0 black)
            drop-shadow(1px 0 0 black) drop-shadow(-1px 0 0 black) drop-shadow(0 1px 0 black) drop-shadow(0 -1px 0 black);
}

